.stage-header{
    position: absolute;
    left: 0;
    margin-top: -5px;
    width: 480px;
    height: 50px;
    background-color: #39436B;
}

.run-button {
    width: 70px;
    height: 30px;
    background-color: #FEAC2A;
    color: white;
    font-size: 13px;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    line-height: 30px;
    margin-top: 10px;
    position: absolute;
    right: 20px;
    border-radius: 5px;
}
