.menu-bar{
    width: 100%;
    height: 50px;
    background-color: #714199;
}
.load-button{
    width: 70px;
    height: 30px;
    background-color: #FEAC2A;
    color: white;
    font-size: 13px;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    line-height: 30px;
    margin-top: 10px;
    position: absolute;
    right: 20px;
    border-radius: 5px;
}
.save-button{
    width: 70px;
    height: 30px;
    background-color: #FEAC2A;
    color: white;
    font-size: 13px;
    text-align: center;
    vertical-align: middle;
    line-height: 30px;
    margin-top: 10px;
    border-radius: 5px;
    display: inline-block;
    position: absolute;
    right: 100px;
}