.blocks-wrapper{
    position: absolute;
    left: 480px;
    right: 0;
    top: 50px;
    bottom: 0;
    font-family: "Helvetica Neue", Helvetica, sans-serif;
    background-color: red;
}


.blocks-wrapper :global(.scratchCategoryMenuItem){
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
}

.blocks-wrapper :global(.scratchCategoryMenuRow){
    width: 100px;
}
.blocks-wrapper :global(.scratchCategoryItemBubble){
    width: 10px;
    height: 10px;
    margin-right:10px ;
    margin-left: 10px;
}

.blocks-wrapper :global(.scratchCategoryMenuItemLabel){
    color: #FFFFFF;
}
.blocks-wrapper :global(.blocklyToolboxDiv){
      width: 100px;
  }

.blocks-wrapper :global(.blocklyFlyout){
    left: 40px;
}

.blocks-wrapper :global(.blocklyScrollbarHandle){
    /*left: 40px;*/
}

.blocks-wrapper :global(.blocklyFlyoutScrollbar){
    left: 45px;
}

.blocks-wrapper :global(.blocklyMainBackground){
    stroke-width: 0px;
}
.blocks-wrapper :global(.blocklyScrollbarHandle){
    fill: #CECDCE;
}

.blocks-wrapper :global(.blocklyScrollbarHandle){
    fill: #CECDCE;
}






